export default {
	props: {
		list: {
			type: Array
		},
		scrollHeight: {
			type: Number
		}
	},
	data() {
		return {
			current: '',
			videoContext: '',
			scrollTop: 0
		}
	},
	methods: {
		edit: function(item) {
			this.$emit('edit', item)
		},
		del: function(item) {
			this.$emit('del', item)
		},
		play: function(id) {
			if (this.current) {
				this.videoContext.pause()
			}
			if (this.current == id) {
				this.videoContext.pause()
				this.current = ''
			} else {

				this.current = id
				this.videoContext = uni.createVideoContext('myVideo' + this.current, this)
				this.videoContext.play()
			}
		},
		lower: function() {
			this.$emit('lower')
		},
		scroll:  function(e) {
			console.log(e.detail.scrollTop)
			if(this.scrollTop < e.detail.scrollTop) {
				this.$emit('isScroll',false)
			} else {
				this.$emit('isScroll',true)
			}
			this.scrollTop = e.detail.scrollTop
		}
	}
}
